.card {
    border-radius: 2px;
    height: 140px;
    margin: 1rem;
    position: relative;
    width: 200px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    -webkit-transition: all .2 ease-in-out;
    -o-transition: all .2 ease-in-out;
    transition: all .2 ease-in-out
  }
  
  .card > .img-container {
    overflow: hidden;
    text-align: center;
  }

  .card > .img-container > img {
    width: 170px; 
    height:118px;
    margin-top: 10px; 
  }
  
.card:hover{
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
}


